.partner {
  margin-bottom: 160px;

  .subtitle {
    margin: 0;
    font-weight: bold;
    font-size: 28px;
    line-height: 149.6%;
    margin-bottom: 44px;
  }

  .customers {
    display: grid;
    grid-template-columns:  repeat(4, 1fr);
    grid-auto-rows: 210px;
    grid-column-gap: 1px;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: rgba(0, 0, 0, 0.1);

    .container {
      @include center;
      background-color: $color_white;
    }
  }
}
