.values {
  padding-top: 130px;
  padding-bottom: 550px;
  background: #030b16 url('../img/values_bg_desk.png') no-repeat bottom center;
  background-size: cover;

  .title {
    color: $color_blue;
  }

  &__title {
    margin-bottom: 10px;
  }

  .list {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto auto;
    grid-column-gap: 105px;
    grid-row-gap: 20px;
    width: 100%;
    margin-bottom: 110px;
  }

  .item {
    display: flex;
    flex-direction: column;
  }

  .number {
    @include font_Furore;
    font-size: 50px;
    letter-spacing: 0.05em;
    color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
    margin-bottom: 10px;
  }

  .subtitle {
    font-weight: bold;
    font-size: 16px;
    line-height: 197.69%;
    color: $color_blue;
    margin-bottom: 10px;
  }

  .text {
    font-size: 14px;
    color: $color_white;
  }
}

.decisions {

  &__title {
    margin-bottom: 60px;
  }

  &__flipping {
    @include center;
    position: absolute;
    top: -50px;
    right: 0;
  }

  &__slider {
    position: relative;
    width: 100%;
    min-width: 0;
    margin-bottom: 120px;
    flex-shrink: 0;

    .flex-viewport {
      min-height: 495px !important;
      padding-left: calc((100% - 450px)/2);
      padding-right: calc((100% - 450px)/2);
      flex-shrink: 0;
    }

    &_container {
      width: 100% !important;
      align-items: center;
      flex-shrink: 0;
    }

    &_item {
      display: flex !important;
      flex-direction: column;
      flex-shrink: 0;
      width: 450px;
      margin: 0 14px 0;
      color: $color_white;

      .button {
        align-self: flex-start;
      }
    }

    &_image {
      width: 100%;
      margin-bottom: 20px;
    }

    &_text {
      font-weight: bold;
      font-size: 22px;
      line-height: 142.6%;
      opacity: 0.9;
      margin-bottom: 20px;
    }

    .flex-direction-nav a.flex-prev {
      background: #212A37 url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.48528 0L0 8.48528L3.95775e-05 8.48532L8.58307e-06 8.48535L8.48529 16.9706L9.8995 15.5564L2.8284 8.48531L9.8995 1.41421L8.48528 0Z' fill='white'/%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 10px 17px;

      &:hover {
        background-color: #3A4657;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.48528 0L0 8.48528L3.95775e-05 8.48532L8.58307e-06 8.48535L8.48529 16.9706L9.8995 15.5564L2.8284 8.48531L9.8995 1.41421L8.48528 0Z' fill='%232482FF'/%3E%3C/svg%3E%0A");
      }
    }

    .flex-direction-nav a.flex-next {
      background: #212A37 url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.51472 0L10 8.48528L9.99996 8.48532L9.99999 8.48535L1.51471 16.9706L0.100496 15.5564L7.1716 8.48531L0.100505 1.41421L1.51472 0Z' fill='white'/%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 10px 17px;

      &:hover {
        background-color: #3A4657;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.51472 0L10 8.48528L9.99996 8.48532L9.99999 8.48535L1.51471 16.9706L0.100496 15.5564L7.1716 8.48531L0.100505 1.41421L1.51472 0Z' fill='%232482FF'/%3E%3C/svg%3E%0A");
      }
    }
  }
}
