.footer {
  background-color: #030B16;
  color: $color_white;

  .wrapper {
    flex-direction: row;
    justify-content: space-between;
  }

  .top {
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 40px 0;

    .wrapper {
      align-items: center;
    }
  }

  .middle {
    padding: 95px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);

    .container {
      flex-direction: column;

      &:first-child {
        margin-right: 100px;
      }
    }

    .row {
      display: flex;
    }

    .column {
      display: flex;
      flex-direction: column;

      &:first-child {
        margin-right: 40px;
      }
    }

    .subtitle {
      font-weight: bold;
      font-size: 16px;
      line-height: 20px;
      text-transform: uppercase;
      opacity: 0.8;
      margin-bottom: 27px;
    }

    .link {
      font-size: 14px;
      line-height: 140%;
      color: $color_white;
      opacity: 0.8;
      transition: .5s;

      &:not(:last-child) {
        margin-bottom: 24px;
      }

      &:hover {
        opacity: 1;
        color: $color_blue;
      }

    }
  }

  .bottom {
    padding: 32px 0 38px;

    .wrapper {
      align-items: center;
    }

    .copyright {
      @include center;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.6);
    }

    .dev_log {
      @include center;
      width: 31px;
      height: 50px;
      background-image: url("data:image/svg+xml,%3Csvg width='31' height='50' viewBox='0 0 31 50' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.5' clip-path='url(%23clip0)'%3E%3Cpath opacity='0.48' d='M0 10.9602L31 29.3318V38.2045L0 19.8329V10.9602Z' fill='white'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M15.5 7.62004L0 16.7015V9.08142L15.5 0L31 8.97704V16.5971L15.5 7.62004ZM15.5 42.2754L31 33.194V40.9184L15.5 49.9998L0 40.9184V33.2984L15.5 42.2754ZM15.6041 15.9705L0.312107 24.9475L15.5 33.8202L30.896 24.8431L15.6041 15.9705Z' fill='white'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0'%3E%3Crect width='31' height='50' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: center;
    }

    .policy {
      @include center;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.6);
      text-decoration: underline;
      transition: .5s;

      &:hover {
        color: $color_blue;
      }

    }
  }

  .soc {
    display: flex;

    .link {
      width: 24px;
      height: 24px;
      background-color: transparent;
      background-repeat: no-repeat;
      background-position: center;
      opacity: 0.5;
      transition: .5s;

      &:not(:last-child) {
        margin-right: 10px;
      }

      &.facebook {
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M23.4375 11.7198C23.4375 5.24804 18.1912 0.000976562 11.7187 0.000976562C5.24634 0.000976562 0 5.24804 0 11.7198C0 18.1921 5.24626 23.4385 11.7187 23.4385C18.1912 23.4385 23.4375 18.1922 23.4375 11.7198ZM14.7569 7.52193H13.195C12.7746 7.52193 12.4792 7.87894 12.4792 8.32406V9.02206H14.7099L14.3487 11.2878H12.4785V17.3611H10.1955V11.2878H8.68056V9.02206H10.1955V7.86676H10.2C10.2402 6.54969 10.6962 5.29569 12.9209 5.21668V5.20833H14.7569V7.52193Z' fill='white'/%3E%3C/svg%3E%0A");
