      &.instagram {
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.8'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M23.9993 11.9318C24.0682 5.46912 18.9673 0.142748 12.5566 0.000711387C6.00811 -0.070307 0.631432 5.18505 0.5625 11.7187C0.5625 18.1814 5.73238 23.3657 12.212 23.4368C18.6226 23.5078 23.9304 18.3945 23.9993 11.9318ZM9.88423 5.3267H14.6783C16.8843 5.3267 18.6733 7.11568 18.6733 9.32173V14.1158C18.6733 16.3218 16.8843 18.1108 14.6783 18.1108H9.88423C7.67818 18.1108 5.8892 16.3218 5.8892 14.1158V9.32173C5.8892 7.11568 7.67818 5.3267 9.88423 5.3267Z' fill='white'/%3E%3Cpath d='M15.4773 11.7187C15.4773 13.4839 14.0464 14.9148 12.2813 14.9148C10.5161 14.9148 9.08523 13.4839 9.08523 11.7187C9.08523 9.95363 10.5161 8.52273 12.2813 8.52273C14.0464 8.52273 15.4773 9.95363 15.4773 11.7187Z' fill='white'/%3E%3Cpath d='M16.5426 7.99006C16.5426 8.28424 16.3041 8.52273 16.0099 8.52273C15.7158 8.52273 15.4773 8.28424 15.4773 7.99006C15.4773 7.69587 15.7158 7.45739 16.0099 7.45739C16.3041 7.45739 16.5426 7.69587 16.5426 7.99006Z' fill='white'/%3E%3C/g%3E%3C/svg%3E%0A");
