.experience {
  margin-bottom: 137px;

  .title {
    margin-bottom: 75px;
  }

  &__flipping {
    @include center;
    position: absolute;
    bottom: -1px;
    left: 542px;
  }

  &__slider {
    position: relative;
    width: 100%;
    min-width: 0;
    flex-shrink: 0;
    margin-bottom: 90px;

    &_container {
      width: 100% !important;
      align-items: center;
      flex-shrink: 0;
    }

    .flex-viewport {
      flex-shrink: 0;
    }

    &_item {
      display: grid !important;
      grid-template-columns: 540px auto;
      grid-column-gap: 55px;
      flex-shrink: 0;
      width: 1400px;
      margin: 0;
      padding: 0;
      background-color: $color_white;
      color: $color_black;

      .container {
        flex-direction: column;
      }

      .subtitle {
        font-weight: bold;
        font-size: 28px;
        line-height: 149.6%;
        opacity: 0.9;
        margin-bottom: 24px;
      }

      .links {
        display: flex;
        margin-bottom: 33px;

        .button_stroke_blue {
          align-self: flex-start;
          min-height: 38px;
          padding: 10px 15px;
          margin-right: 15px;
          font-weight: normal;
          font-size: 12px;
          line-height: 150.9%;
          text-transform: none;
          color: $color_black;
          border-radius: 1px;

          &:hover {
            color: $color_white;
          }
        }
      }

      .list {
        flex-direction: column;
        margin-bottom: 37px;
      }

      .item {
        position: relative;
        line-height: 160%;
        padding-left: 36px;

        &::before {
          content: '';
          position: absolute;
          top: 5px;
          left: 0;
          width: 8px;
          height: 8px;
          background-color: $color_blue;
          border: 4px solid #d3e6ff;
        }

        &:not(:last-child) {
          margin-bottom: 30px;
        }
      }

      .bottom {
        display: flex;

        .button_bg_blue {
          margin-right: 40px;
        }

        .button_stroke_blue {
          border-width: 1px;
        }
      }
    }
  }

  .customers {
    display: grid;
    grid-template-columns:  repeat(5, 1fr);
    grid-auto-rows: 155px;
    grid-column-gap: 1px;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: rgba(0, 0, 0, 0.1);

    .container {
      @include center;
      background-color: $color_white;
    }
  }
}
