@mixin font_Montserrat {
  font-family: Montserrat, Arial, sans-serif;
}

@mixin font_Roboto {
  font-family: Roboto, Arial, sans-serif;
}

@mixin font_Furore {
  font-family: Furore, Arial, sans-serif;
}

@mixin font_Intro {
  font-family: Intro, Arial, sans-serif;
}

@mixin center {
  display: flex;
  align-items: center;
  justify-content: center;
}
