.header {
  position: sticky;
  z-index: 90;
  top: 0;
  background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);

  &__shadow {
    background-color: #030B16;
  }

  .wrapper {
    min-height: 75px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .menu {
    color: $color_white;

    .item {
      @include center;
      min-height: 42px;
      margin-right: 40px;

      &:first-child {
        margin-left: 40px;
      }
    }

    .link {
      color: $color_white;
      transition: .5s;

      &:hover {
        color: $color_blue;
      }
    }
  }
}
