.services {
  padding-top: 100px;
  margin-bottom: 140px;

  .title {
    color: $color_black;
    margin-bottom: 80px;
  }

  .content {
    margin-bottom: 60px;
  }

  .navigation {
    margin-right: 70px;
  }

  .list {
    flex-direction: column;
  }

  .item {
    @include font_Roboto;
    display: flex;
    min-width: 328px;
    min-height: 105px;
    margin-bottom: 5px;
    font-size: 16px;
    line-height: 140%;
    color: $color_gray;
    cursor: pointer;

    .number {
      @include center;
      min-width: 37px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      transition: .5s;

      .rotate {
        transform: rotate(-90deg);
      }
    }

    .text {
      @include center;
      width: 100%;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-left: none;
      padding: 0 24px 0;
      transition: .5s;
    }

    &:hover,
    &:focus {
      color: $color_blue;

      .number,
      .text {
        border-color: $color_blue;
      }
    }

    &.active {
      .number {
        background-color: $color_blue;
        color: $color_white;
      }
    }
  }

  .block {
    display: flex;
    flex-direction: column;
    color: $color_black;

    .subtitle {
      font-weight: bold;
      font-size: 28px;
      margin-bottom: 28px;
    }

    .image {
      width: 100%;
      margin-bottom: 32px;
    }

    .text {
      line-height: 160%;
      margin-bottom: 52px;
    }

    .button {
      align-self: flex-start;
    }
  }

  .banner {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    padding: 60px 90px 0;
    background: $color_blue url('../img/turbine.png') no-repeat left top;
    background-size: contain;
    color: $color_white;

    .content {
      flex-direction: column;
      max-width: 900px;
    }

    .subtitle {
      font-weight: bold;
      font-size: 24px;
      margin-bottom: 20px;
    }

    .text {
      line-height: 160%;
    }
  }


}
