.landing {
  position: relative;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin-top: -77px;

  &::before {
    z-index: 15;
    content: '';
    position: absolute;
    width: 36px;
    height: 36px;
    bottom: 70px;
    left: 70px;
    background-image: url("data:image/svg+xml,%3Csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.5'%3E%3Ccircle cx='1.54545' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='1.54545' cy='34' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='34' r='1.54545' fill='white'/%3E%3C/g%3E%3C/svg%3E");
  }

  .media {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: #090909 url('../img/landing_bg_desk.png') no-repeat center center;
    background-size: cover;
  }

  .video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%);
  }

  .wrapper {
    z-index: 10;
    height: 100%;
    padding-top: 180px;
    color: $color_white;
  }

  .headline {
    position: relative;
    @include font_Furore;
    font-size: 136px;
    color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
    margin-bottom: 90px;


    &::before {
      content: '';
      position: absolute;
      width: 68px;
      height: 68px;
      top: 36px;
      right: -155px;
      background-image: url("data:image/svg+xml,%3Csvg width='68' height='68' viewBox='0 0 68 68' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.5'%3E%3Ccircle cx='1.54545' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='1.54545' cy='34' r='1.54545' fill='white'/%3E%3Ccircle cx='1.54545' cy='66.4545' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='66.4546' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='34' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='66.4545' r='1.54545' fill='white'/%3E%3Ccircle cx='66.4546' cy='34' r='1.54545' fill='white'/%3E%3Ccircle cx='66.4546' cy='66.4545' r='1.54545' fill='white'/%3E%3C/g%3E%3C/svg%3E");
    }
  }

  .content {
    justify-content: space-between;
  }

  .container {
    flex-direction: column;
  }

  .title {
    font-size: 54px;
    margin-bottom: 30px;
    letter-spacing: 0.05em;
  }

  .text {
    font-size: 18px;
    margin-bottom: 90px;
  }

  .list {
    flex-direction: column;
  }

  .item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;

    .theses {
      width: 310px;
      font-size: 14px;
      line-height: 182.19%;
      margin-top: 10px;
    }
  }

  .number {
    @include center;
    @include font_Furore;
    min-width: 60px;
    margin-right: 30px;
    font-size: 90px;
    line-height: 136.6%;
    color: $color_blue;
  }

  .link {
    color: $color_blue;
  }

  .button {
    align-self: flex-start;
    margin-bottom: 170px;
  }
}
