* {
  box-sizing: border-box;
  // outline: 1px solid red;
}

body {
  @include font_Montserrat;
  font-size: 16px;
  color: $color_black;
}

ul, h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

ul {
  display: flex;
  list-style: none;
}

a {
  text-decoration: none;
}

button,
.button {
  @include center;
  @include font_Montserrat;
  background-color: transparent;
  border-radius: 0;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 1400px;
  margin: 0 auto;
}

.content,
.container {
  display: flex;
}

.title {
  @include font_Furore;
  font-size: 48px;
  font-weight: 400;
  line-height: 136.6%;
  letter-spacing: 0.05em;
}

.color_blue {
  color: $color_blue;
}


.logo {
  display: flex;
  align-items: center;
  color: $color_white;

  .content {
    flex-direction: column;
  }

  .image {
    width: 37px;
    height: 37px;
    margin-right: 13px;
  }

  .name {
    font-size: 18px;
    line-height: 18px;
    text-transform: uppercase;
  }

  .text {
    font-size: 10px;
    line-height: 12px;
  }
}

.communication {
  display: flex;
  align-items: center;

  .container {
    flex-direction: column;
    margin-right: 50px;
  }

  .tel {
    font-weight: bold;
    font-size: 18px;
    color: $color_white;
    margin-right: 28px;
  }

  .email {
    margin-top: 7px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: $color_white;
  }

  .button {
    min-height: 42px;
    padding: 0 15px 0;
    font-size: 14px;
    color: $color_white;
    border: 1px solid rgba(36, 130, 255, 0.5);
    border-left-color: rgba(36, 130, 255, 1);
    box-shadow: -2px 0px 0px 0px rgba(36, 130, 255, 1);
    transition: .5s;

    &:hover,
    &:focus {
      background-color: rgba(36, 130, 255, 0.5);
      border-color: rgba(36, 130, 255, 0);
      border-right-color: rgba(36, 130, 255, 1);
      box-shadow: 2px 0px 0px 0px rgba(36, 130, 255, 1);
      outline: none;
    }

    &:active {
      background-color: transparent;
      border: 1px solid rgba(36, 130, 255, 0.5);
      border-left-color: rgba(36, 130, 255, 1);
      box-shadow: -2px 0px 0px 0px rgba(36, 130, 255, 1);
    }

    &:disabled {
      opacity: 0.5;
    }
  }

}

.checkbox {
  display: none;

  &__label {
    position: relative;
    display: flex;
    width: 100%;
    padding-left: 30px;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      display: flex;
      top: 0;
      left: 0;
      width: 17px;
      height: 17px;
      border: 1px solid rgba(255, 255, 255, 0.5);
    }
  }

  &:checked + .checkbox__label::before {
    background-color: #ffffff;
    background-image: url("data:image/svg+xml,%3Csvg width='9' height='6' viewBox='0 0 9 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M2.28149 4.66748L2.28136 4.66761L3.34202 5.72827L3.34215 5.72814L3.34224 5.72823L4.4029 4.66757L4.40281 4.66748L8.00963 1.06066L6.94897 0L3.34215 3.60682L1.10656 1.37122L0.0458984 2.43188L2.28149 4.66748Z' fill='%232482FF'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: center center;
    border: none;
  }
}

.input {
  min-height: 70px;
  padding: 0 30px;
  background-color: rgba(252, 252, 252, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: $color_white;

  &::placeholder {
    color: $color_white;
    opacity: 1;
  }

  &:focus {
    border: 1px solid rgba(255, 255, 255, 0.7);
    box-shadow: none;
    outline: none;
  }

  // &:invalid {
  //   border: 1px solid #FF5A5A;
  // }
}

.radio {

  &__input {
    display: none;
  }

  &__label {
    display: flex;
    width: 100%;
  }

}

.button_bg_blue {
  min-height: 60px;
  padding: 0 40px 0;
  background-color: $color_blue;
  color: $color_white;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  transition: .5s;

  &:hover,
  &:focus {
    background-color: $color_darkblue;
    outline: none;
    box-shadow: none;
  }

  &:active {
    background-color: $color_lightblue;
  }

  &:disabled {
    opacity: 0.3;
  }
}

.button_stroke_white {
  min-height: 60px;
  padding: 0 40px 0;
  border: 2px solid $color_white;
  color: $color_white;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  transition: .5s;

  &:hover {
    background-color: $color_white;
    color: $color_blue;
  }

  &:active {
    background-color: transparent;
    color: $color_white;
  }

  &:disabled {
    opacity: 0.3;
  }
}

.button_stroke_blue {
  min-height: 60px;
  padding: 0 40px 0;
  border: 2px solid $color_blue;
  color: $color_blue;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  transition: .5s;

  &:hover {
    background-color: $color_blue;
    color: $color_white;
  }

  &:active {
    background-color: transparent;
    color: $color_blue;
  }

  &:disabled {
    opacity: 0.3;
  }
}

.flex-direction-nav a {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 77px;
  height: 77px;
  margin: 0;
  opacity: 1;
  border: none;
  box-shadow: none;
  background-color: #FBFBFB;
  transition: none;

  &.flex-prev,
  &.flex-next {
    transition: .5s;
  }

  &.flex-prev {
    background-image: url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.48528 0L0 8.48528L3.95775e-05 8.48532L8.58307e-06 8.48535L8.48529 16.9706L9.8995 15.5564L2.8284 8.48531L9.8995 1.41421L8.48528 0Z' fill='%230C1C32'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 10px 17px;

    &:hover {
      background-image: url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.48528 0L0 8.48528L3.95775e-05 8.48532L8.58307e-06 8.48535L8.48529 16.9706L9.8995 15.5564L2.8284 8.48531L9.8995 1.41421L8.48528 0Z' fill='%232482FF'/%3E%3C/svg%3E%0A");
    }

    &:focus {
      outline: none;
    }

    &::before {
      display: none;
    }
  }

  &.flex-next {
    background-image: url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.51472 0L10 8.48528L9.99996 8.48532L9.99999 8.48535L1.51471 16.9706L0.100496 15.5564L7.1716 8.48531L0.100505 1.41421L1.51472 0Z' fill='%230C1C32'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 10px 17px;

    &:hover {
      background-image: url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.51472 0L10 8.48528L9.99996 8.48532L9.99999 8.48535L1.51471 16.9706L0.100496 15.5564L7.1716 8.48531L0.100505 1.41421L1.51472 0Z' fill='%232482FF'/%3E%3C/svg%3E%0A");
    }

    &:focus {
      outline: none;
    }

    &::before {
      display: none;
    }
  }

  &.flex-prev {
    top: -77px;
    right: 0;
    left: -154px;
  }

  &.flex-next {
    top: -77px;
    right: 0;
    left: -77px;
  }

  &.flex-disabled {
    opacity: 0.5 !important;
    cursor: default;
    pointer-events: none;
  }
}

.flex-control-nav {
  display: flex;
  align-items: center;
  justify-content: center;
}

.display_off {
  display: none !important;
}

.display_on  {
  display: flex !important;
}

.overlay {
  overflow: hidden;
  display: none;
  position: fixed;
  z-index: 95;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  opacity: 0;
  -webkit-transition: opacity .5s;
  -moz-transition: opacity .5s;
  -ms-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
}
