.contacts {
  margin-bottom: 120px;

  .wrapper {
    flex-direction: row;
  }

  .content {
    display: flex;
    flex-direction: column;
    margin-right: 75px;

    .link {
      font-weight: bold;
      font-size: 18px;
      line-height: 197.69%;
      text-decoration: underline;
      color: $color_blue;
      transition: .5s;

      &:hover {
        color: #4394FF;
      }
    }
  }

  .title {
    margin-bottom: 64px;
  }

  .form {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    max-width: 635px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: $color_blue;
    color: $color_white;
    padding: 40px 58px 60px;

    &__title {
      font-weight: bold;
      font-size: 28px;
      line-height: 149.6%;
      opacity: 0.9;
      margin-bottom: 36px;
    }

    .input {
      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &:last-child {
        margin-bottom: 40px;
      }
    }

    .button_stroke_white {
      margin-bottom: 30px;
    }
  }

  .consent {
    display: flex;
    font-size: 12px;
    line-height: 164.69%;

    .link {
      color: $color_white;
      text-decoration-line: underline;
    }
  }

  .title {
    margin-bottom: 64px;
  }

  .persona {
    display: flex;
    align-items: center;
    margin-bottom: 24px;

    .image {
      margin-right: 22px;
    }

    .name {
      font-weight: bold;
      font-size: 20px;
      line-height: 149.6%;
    }

    .position {
      font-size: 14px;
      line-height: 197.69%;
    }
  }

  .list {
    flex-direction: column;
    list-style-type: disc;
    list-style-position: inside;
    margin-bottom: 36px;
  }

  .item {
    font-size:16px;
    line-height: 160%;
    padding-left: 0;

    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }
}
