.technologies {
  overflow: hidden;
  padding-top: 115px;
  background-color: $color_blue;
  color: $color_white;

  .title {
    margin-bottom: 70px;
  }

  .list {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto auto;
    width: 100%;
    margin-bottom: 70px;
  }

  .item {
    display: flex;
    flex-direction: column;
    padding: 70px 20px 40px 40px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.1);
  }

  .container {
    display: flex;
    align-items: center;
    min-height: 85px;
    margin-bottom: 10px;
  }

  .subtitle {
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 10px;
  }

  .text {
    display: flex;
    max-width: 390px;
    font-size: 14px;
    line-height: 167%;
  }

  .caption {
    @include font_Furore;
    display: flex;
    flex-direction: column;
    font-size: 80px;
    line-height: 136.6%;
    letter-spacing: 0.05em;
  }

  .line {
    white-space: nowrap;

    &:first-child {
      margin-left: -390px;
      margin-bottom: 20px;
    }

    &:nth-child(2) {
      margin-left: 40px;
      margin-bottom: 20px;
    }

    &:last-child {
      margin-left: 10px;
      margin-bottom: 95px;
    }
  }

  .stroke {
    color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
    margin: 0 40px 0;
  }
}
