.timeline {
  margin-top: -500px;
  margin-bottom: 130px;
  color: $color_white;

  .title {
    color: $color_blue;
    margin-bottom: 30px;
  }

  .text {
    width: 100%;
    font-size: 18px;
    line-height: 165.69%;
    margin-bottom: 60px;
  }

  &__flipping {
    @include center;
    position: absolute;
    bottom: 0;
    left: 154px;
  }

  &__slider {
    position: relative;
    width: 100%;
    min-width: 0;
    flex-shrink: 0;


    &_container {
      width: 100% !important;
      align-items: center;
      flex-shrink: 0;
    }

    .flex-viewport {
      flex-shrink: 0;
    }

    &_item {
      display: grid !important;
      grid-template-columns: auto auto;
      grid-column-gap: 40px;
      flex-shrink: 0;
      width: 1400px;
      margin: 0;
      padding: 0;
      background-color: $color_blue;
      color: $color_white;

      .container {
        flex-direction: column;
        margin: 60px 70px 70px;
      }

      .subtitle {
        font-weight: bold;
        font-size: 28px;
        line-height: 149.6%;
        margin-bottom: 30px;
      }

      .links {
        display: flex;
      }

      .button_stroke_white {
        align-self: flex-start;
        min-height: 38px;
        padding: 10px 15px;
        margin-right: 15px;
        font-weight: normal;
        font-size: 12px;
        line-height: 150.9%;
        text-transform: none;
      }

      .indicators {
        &:not(:last-child) {
          margin-bottom: 33px;
        }
      }

      .number {
        @include font_Furore;
        font-size: 40px;
        line-height: 136.6%;
        letter-spacing: 0.05em;
        margin-bottom: 10px;
      }

      .name {
        font-weight: 500;
        font-size: 16px;
        line-height: 197.69%;
        opacity: 0.7;
      }
    }
  }
}
